'use client'

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Progress } from '@/components/ui/progress'
import { Music, Trophy, Shirt, Users, ExternalLink, Share2, Heart } from 'lucide-react'
import Link from 'next/link'

export interface RWAToken {
  tokenId: string
  rwaType: string
  percent: string
  metadataURI: string
  balance?: string
  totalSupply?: string
  currentPrice?: string
  priceChange?: string
  description?: string
  title?: string
  image?: string
  category?: string
  verified?: boolean
}

interface RWACardProps {
  token: RWAToken
  showActions?: boolean
  compact?: boolean
}

const getRWAIcon = (type: string) => {
  switch (type.toLowerCase()) {
    case 'music_right':
    case 'music':
      return <Music className="h-5 w-5" />
    case 'player_right':
    case 'player':
      return <Trophy className="h-5 w-5" />
    case 'merchandise':
    case 'merch':
      return <Shirt className="h-5 w-5" />
    case 'fan_token':
    case 'fan':
      return <Users className="h-5 w-5" />
    default:
      return <Trophy className="h-5 w-5" />
  }
}

const getRWAColor = (type: string) => {
  switch (type.toLowerCase()) {
    case 'music_right':
    case 'music':
      return 'bg-purple-100 text-purple-800'
    case 'player_right':
    case 'player':
      return 'bg-indigo-100 text-indigo-800'
    case 'merchandise':
    case 'merch':
      return 'bg-green-100 text-green-800'
    case 'fan_token':
    case 'fan':
      return 'bg-orange-100 text-orange-800'
    default:
      return 'bg-gray-100 text-gray-800'
  }
}

const formatRWAType = (type: string) => {
  return type.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase())
}

export const RWACard: React.FC<RWACardProps> = ({ 
  token, 
  showActions = true, 
  compact = false 
}) => {
  const ownership = parseFloat(token.percent) || 0
  const priceChange = parseFloat(token.priceChange || '0')
  const isPositive = priceChange >= 0

  return (
    <Card className={`group hover:shadow-xl transition-all duration-300 border-0 shadow-lg ${compact ? 'h-auto' : 'h-full'}`}>
      <CardHeader className="pb-4">
        <div className="flex items-start justify-between">
          <div className="flex items-center gap-3">
            <div className={`p-2 rounded-full ${getRWAColor(token.rwaType)}`}>
              {getRWAIcon(token.rwaType)}
            </div>
            <div>
              <CardTitle className="text-lg group-hover:text-purple-600 transition-colors">
                {token.title || `RWA #${token.tokenId}`}
              </CardTitle>
              <CardDescription className="text-sm">
                {formatRWAType(token.rwaType)}
              </CardDescription>
            </div>
          </div>
          <div className="flex items-center gap-2">
            {token.verified && (
              <Badge variant="secondary" className="bg-green-100 text-green-800">
                Verified
              </Badge>
            )}
            <Badge className={getRWAColor(token.rwaType)}>
              {ownership}%
            </Badge>
          </div>
        </div>
      </CardHeader>

      <CardContent className="space-y-4">
        {/* Image placeholder */}
        {token.image && (
          <div className="aspect-video bg-gradient-to-br from-purple-100 to-indigo-100 rounded-lg flex items-center justify-center">
            <img 
              src={token.image} 
              alt={token.title || `RWA ${token.tokenId}`}
              className="w-full h-full object-cover rounded-lg"
            />
          </div>
        )}

        {/* Description */}
        {token.description && (
          <p className="text-sm text-gray-600 line-clamp-2">
            {token.description}
          </p>
        )}

        {/* Ownership Progress */}
        <div className="space-y-2">
          <div className="flex justify-between text-sm">
            <span className="text-gray-600">Ownership</span>
            <span className="font-medium">{ownership}%</span>
          </div>
          <Progress value={ownership} className="h-2" />
        </div>

        {/* Price Info */}
        <div className="grid grid-cols-2 gap-4">
          <div>
            <div className="text-sm text-gray-600">Current Price</div>
            <div className="font-semibold">
              {token.currentPrice || '0.00'} CHZ
            </div>
          </div>
          <div>
            <div className="text-sm text-gray-600">24h Change</div>
            <div className={`font-semibold ${isPositive ? 'text-green-600' : 'text-red-600'}`}>
              {isPositive ? '+' : ''}{priceChange.toFixed(2)}%
            </div>
          </div>
        </div>

        {/* Token Info */}
        <div className="grid grid-cols-2 gap-4 text-sm">
          <div>
            <span className="text-gray-600">Your Balance:</span>
            <div className="font-medium">{token.balance || '0'} tokens</div>
          </div>
          <div>
            <span className="text-gray-600">Total Supply:</span>
            <div className="font-medium">{token.totalSupply || 'N/A'}</div>
          </div>
        </div>

        {/* Actions */}
        {showActions && (
          <div className="flex gap-2 pt-2">
            <Link href={`/rwa/${token.tokenId}`} className="flex-1">
              <Button className="w-full bg-purple-600 hover:bg-purple-700">
                View Details
              </Button>
            </Link>
            <Button variant="outline" size="sm" className="px-3">
              <Heart className="h-4 w-4" />
            </Button>
            <Button variant="outline" size="sm" className="px-3">
              <Share2 className="h-4 w-4" />
            </Button>
            <Button variant="outline" size="sm" className="px-3">
              <ExternalLink className="h-4 w-4" />
            </Button>
          </div>
        )}
      </CardContent>
    </Card>
  )
} 