import { useState, useCallback } from 'react'
import { useWallet } from '@/contexts/wallet-context'
import { toast } from 'sonner'

const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3001'

export interface RWAApiResponse {
  success: boolean
  transactionHash?: string
  blockNumber?: number
  gasUsed?: string
  error?: string
}

export interface RWATokenData {
  tokenId: string
  rwaType: string
  percent: string
  metadataURI: string
  balance: string
}

export interface UserTokensResponse {
  address: string
  tokens: RWATokenData[]
}

export interface MintRWARequest {
  to: string
  amount: number
  rwaType: string
  percent: number
  metadataURI: string
}

export interface BalanceResponse {
  address: string
  tokenId: string
  balance: string
}

export interface ContractInfoResponse {
  address: string
  owner: string
  network: string
  chainId: number
}

export const useRWAApi = () => {
  const { address } = useWallet()
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const handleApiCall = useCallback(async <T>(
    apiCall: () => Promise<Response>,
    successMessage?: string,
    errorMessage?: string
  ): Promise<T | null> => {
    setLoading(true)
    setError(null)

    try {
      const response = await apiCall()
      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'API request failed')
      }

      if (successMessage) {
        toast.success(successMessage)
      }

      return data as T
    } catch (err) {
      const errorMsg = err instanceof Error ? err.message : 'An error occurred'
      setError(errorMsg)
      
      if (errorMessage) {
        toast.error(errorMessage)
      }
      
      return null
    } finally {
      setLoading(false)
    }
  }, [])

  // Health check
  const checkHealth = useCallback(async () => {
    return handleApiCall<{ message: string; contract: string; network: string }>(
      () => fetch(`${API_BASE_URL}/`),
      'API is healthy',
      'Failed to check API health'
    )
  }, [handleApiCall])

  // Get contract info
  const getContractInfo = useCallback(async () => {
    return handleApiCall<ContractInfoResponse>(
      () => fetch(`${API_BASE_URL}/contract/info`),
      undefined,
      'Failed to get contract info'
    )
  }, [handleApiCall])

  // Mint RWA token
  const mintRWA = useCallback(async (request: MintRWARequest) => {
    return handleApiCall<RWAApiResponse>(
      () => fetch(`${API_BASE_URL}/mint`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(request)
      }),
      'RWA token minted successfully!',
      'Failed to mint RWA token'
    )
  }, [handleApiCall])

  // Get user balance for a specific token
  const getBalance = useCallback(async (tokenId: string, userAddress?: string) => {
    const targetAddress = userAddress || address
    if (!targetAddress) {
      toast.error('Wallet not connected')
      return null
    }

    return handleApiCall<BalanceResponse>(
      () => fetch(`${API_BASE_URL}/balance/${targetAddress}/${tokenId}`),
      undefined,
      'Failed to get balance'
    )
  }, [handleApiCall, address])

  // Get batch balances
  const getBatchBalances = useCallback(async (addresses: string[], tokenIds: string[]) => {
    return handleApiCall<{ balances: string[] }>(
      () => fetch(`${API_BASE_URL}/balances/batch`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ addresses, tokenIds })
      }),
      undefined,
      'Failed to get batch balances'
    )
  }, [handleApiCall])

  // Get RWA metadata
  const getRWAData = useCallback(async (tokenId: string) => {
    return handleApiCall<{
      tokenId: string
      rwaType: string
      percent: string
      metadataURI: string
    }>(
      () => fetch(`${API_BASE_URL}/rwa/${tokenId}`),
      undefined,
      'Failed to get RWA data'
    )
  }, [handleApiCall])

  // Get token URI
  const getTokenURI = useCallback(async (tokenId: string) => {
    return handleApiCall<{ tokenId: string; uri: string }>(
      () => fetch(`${API_BASE_URL}/uri/${tokenId}`),
      undefined,
      'Failed to get token URI'
    )
  }, [handleApiCall])

  // Get all user tokens
  const getUserTokens = useCallback(async (userAddress?: string) => {
    const targetAddress = userAddress || address
    if (!targetAddress) {
      toast.error('Wallet not connected')
      return null
    }

    return handleApiCall<UserTokensResponse>(
      () => fetch(`${API_BASE_URL}/user/${targetAddress}/tokens`),
      undefined,
      'Failed to get user tokens'
    )
  }, [handleApiCall, address])

  // Transfer token
  const transferToken = useCallback(async (
    from: string,
    to: string,
    tokenId: string,
    amount: number
  ) => {
    return handleApiCall<RWAApiResponse>(
      () => fetch(`${API_BASE_URL}/transfer`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ from, to, tokenId, amount })
      }),
      'Token transferred successfully!',
      'Failed to transfer token'
    )
  }, [handleApiCall])

  // Utility function to create metadata URI
  const createMetadataURI = useCallback((metadata: {
    name: string
    description: string
    image?: string
    attributes?: Array<{ trait_type: string; value: string }>
  }) => {
    // In a real app, this would upload to IPFS
    const mockHash = Date.now().toString(36) + Math.random().toString(36).substr(2)
    return `ipfs://mock-${mockHash}.json`
  }, [])

  // Utility function to format RWA type
  const formatRWAType = useCallback((type: string) => {
    return type.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase())
  }, [])

  // Utility function to calculate portfolio value
  const calculatePortfolioValue = useCallback((tokens: RWATokenData[]) => {
    // Mock calculation - in real app, this would use actual market prices
    return tokens.reduce((total, token) => {
      const mockPrice = 15.50 // Mock price per token
      const balance = parseInt(token.balance) || 0
      return total + (balance * mockPrice)
    }, 0)
  }, [])

  return {
    // State
    loading,
    error,
    
    // API methods
    checkHealth,
    getContractInfo,
    mintRWA,
    getBalance,
    getBatchBalances,
    getRWAData,
    getTokenURI,
    getUserTokens,
    transferToken,
    
    // Utility methods
    createMetadataURI,
    formatRWAType,
    calculatePortfolioValue,
    
    // Clear error
    clearError: () => setError(null)
  }
} 