'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Badge } from '@/components/ui/badge'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { RWACard, RWAToken } from '@/components/rwa-card'
import { useRWAApi } from '@/hooks/use-rwa-api'
import { useWallet } from '@/contexts/wallet-context'
import { Search, Filter, TrendingUp, Music, Trophy, Shirt, Users } from 'lucide-react'
import { toast } from 'sonner'

export default function MarketplacePage() {
  const { address } = useWallet()
  const { getUserTokens, getContractInfo, loading: apiLoading } = useRWAApi()
  const [tokens, setTokens] = useState<RWAToken[]>([])
  const [filteredTokens, setFilteredTokens] = useState<RWAToken[]>([])
  const [allTokens, setAllTokens] = useState<RWAToken[]>([])
  const [contractInfo, setContractInfo] = useState<any>(null)
  const [searchTerm, setSearchTerm] = useState('')
  const [categoryFilter, setCategoryFilter] = useState('all')
  const [sortBy, setSortBy] = useState('newest')
  const [loading, setLoading] = useState(true)

  // Fetch all RWA tokens from the blockchain
  const fetchAllTokens = async () => {
    try {
      setLoading(true)
      
      // Get contract info
      const contractData = await getContractInfo()
      setContractInfo(contractData)
      
      // For now, we'll scan the first 50 token IDs to find existing RWAs
      const tokenPromises = []
      for (let i = 1; i <= 50; i++) {
        tokenPromises.push(
          fetch(`http://localhost:3001/rwa/${i}`)
            .then(response => response.json())
            .then(data => {
              if (data.rwaType && data.rwaType !== '') {
                return {
                  tokenId: i.toString(),
                  rwaType: data.rwaType,
                  percent: data.percent,
                  metadataURI: data.metadataURI,
                  balance: '0',
                  totalSupply: '100', // Mock for now
                  currentPrice: (Math.random() * 50 + 10).toFixed(2),
                  priceChange: (Math.random() * 10 - 5).toFixed(2),
                  title: `RWA Token #${i}`,
                  description: `${data.rwaType.replace(/_/g, ' ')} asset with ${data.percent}% ownership`,
                  verified: Math.random() > 0.5
                }
              }
              return null
            })
            .catch(() => null)
        )
      }
      
      const results = await Promise.all(tokenPromises)
      const validTokens = results.filter(token => token !== null) as RWAToken[]
      
      setAllTokens(validTokens)
      setTokens(validTokens)
      
      if (validTokens.length === 0) {
        toast.info('No RWA tokens found. Create the first one!')
      }
      
    } catch (error) {
      console.error('Error fetching tokens:', error)
      toast.error('Failed to load RWA tokens')
    } finally {
      setLoading(false)
    }
  }

  // Load tokens on component mount
  useEffect(() => {
    fetchAllTokens()
  }, [])

  // Filter and search logic
  useEffect(() => {
    let filtered = allTokens

    // Search filter
    if (searchTerm) {
      filtered = filtered.filter(token => 
        token.title?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        token.description?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        token.rwaType.toLowerCase().includes(searchTerm.toLowerCase())
      )
    }

    // Category filter
    if (categoryFilter !== 'all') {
      filtered = filtered.filter(token => token.rwaType === categoryFilter)
    }

    // Sort
    switch (sortBy) {
      case 'price_high':
        filtered.sort((a, b) => parseFloat(b.currentPrice || '0') - parseFloat(a.currentPrice || '0'))
        break
      case 'price_low':
        filtered.sort((a, b) => parseFloat(a.currentPrice || '0') - parseFloat(b.currentPrice || '0'))
        break
      case 'performance':
        filtered.sort((a, b) => parseFloat(b.priceChange || '0') - parseFloat(a.priceChange || '0'))
        break
      case 'ownership':
        filtered.sort((a, b) => parseFloat(b.percent) - parseFloat(a.percent))
        break
      default:
        // newest - keep original order
        break
    }

    setFilteredTokens(filtered)
    setTokens(filtered)
  }, [allTokens, searchTerm, categoryFilter, sortBy])

  const categories = [
    { value: 'all', label: 'All Categories', icon: Filter },
    { value: 'music_right', label: 'Music Rights', icon: Music },
    { value: 'player_right', label: 'Player Rights', icon: Trophy },
    { value: 'merchandise', label: 'Merchandise', icon: Shirt },
    { value: 'fan_token', label: 'Fan Tokens', icon: Users }
  ]

  const getMarketStats = () => {
    const totalValue = tokens.reduce((sum, token) => sum + parseFloat(token.currentPrice || '0'), 0)
    const avgChange = tokens.reduce((sum, token) => sum + parseFloat(token.priceChange || '0'), 0) / tokens.length
    return { totalValue, avgChange }
  }

  const stats = getMarketStats()

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-50 to-indigo-50">
      {/* Header */}
      <div className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-6 py-8">
          <div className="flex items-center justify-between mb-6">
            <div>
              <h1 className="text-4xl font-bold text-gray-900">RWA Marketplace</h1>
              <p className="text-lg text-gray-600 mt-2">
                Discover and invest in tokenized real-world assets
              </p>
            </div>
            <div className="flex items-center gap-4">
              <Card className="border-0 shadow-lg">
                <CardContent className="p-4 text-center">
                  <div className="text-2xl font-bold text-purple-600">
                    ${stats.totalValue.toFixed(0)}
                  </div>
                  <div className="text-sm text-gray-600">Market Cap</div>
                </CardContent>
              </Card>
              <Card className="border-0 shadow-lg">
                <CardContent className="p-4 text-center">
                  <div className={`text-2xl font-bold ${stats.avgChange >= 0 ? 'text-green-600' : 'text-red-600'}`}>
                    {stats.avgChange >= 0 ? '+' : ''}{stats.avgChange.toFixed(2)}%
                  </div>
                  <div className="text-sm text-gray-600">Avg Change</div>
                </CardContent>
              </Card>
            </div>
          </div>

          {/* Filters */}
          <div className="flex flex-col sm:flex-row gap-4 items-center">
            <div className="relative flex-1">
              <Search className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
              <Input
                placeholder="Search RWAs..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10"
              />
            </div>
            
            <Select value={categoryFilter} onValueChange={setCategoryFilter}>
              <SelectTrigger className="w-full sm:w-[200px]">
                <SelectValue placeholder="Select category" />
              </SelectTrigger>
              <SelectContent>
                {categories.map(category => (
                  <SelectItem key={category.value} value={category.value}>
                    <div className="flex items-center gap-2">
                      <category.icon className="h-4 w-4" />
                      {category.label}
                    </div>
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>

            <Select value={sortBy} onValueChange={setSortBy}>
              <SelectTrigger className="w-full sm:w-[150px]">
                <SelectValue placeholder="Sort by" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="newest">Newest</SelectItem>
                <SelectItem value="price_high">Price: High to Low</SelectItem>
                <SelectItem value="price_low">Price: Low to High</SelectItem>
                <SelectItem value="performance">Best Performance</SelectItem>
                <SelectItem value="ownership">Highest Ownership</SelectItem>
              </SelectContent>
            </Select>
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="max-w-7xl mx-auto px-6 py-8">
        {/* Category badges */}
        <div className="flex flex-wrap gap-2 mb-6">
          {categories.map(category => (
            <Badge
              key={category.value}
              variant={categoryFilter === category.value ? "default" : "secondary"}
              className="cursor-pointer px-4 py-2"
              onClick={() => setCategoryFilter(category.value)}
            >
              <category.icon className="h-3 w-3 mr-2" />
              {category.label}
            </Badge>
          ))}
        </div>

        {/* Results */}
        <div className="mb-6">
          <p className="text-gray-600">
            Showing {filteredTokens.length} of {tokens.length} RWAs
          </p>
        </div>

        {/* RWA Grid */}
        {loading ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {[...Array(6)].map((_, i) => (
              <Card key={i} className="animate-pulse">
                <CardHeader>
                  <div className="h-4 bg-gray-200 rounded w-3/4"></div>
                  <div className="h-3 bg-gray-200 rounded w-1/2"></div>
                </CardHeader>
                <CardContent>
                  <div className="h-40 bg-gray-200 rounded mb-4"></div>
                  <div className="space-y-2">
                    <div className="h-3 bg-gray-200 rounded"></div>
                    <div className="h-3 bg-gray-200 rounded w-5/6"></div>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredTokens.map(token => (
              <RWACard key={token.tokenId} token={token} />
            ))}
          </div>
        )}

        {/* Empty state */}
        {!loading && filteredTokens.length === 0 && (
          <div className="text-center py-12">
            <div className="text-gray-400 text-6xl mb-4">üîç</div>
            <h3 className="text-xl font-semibold text-gray-900 mb-2">No RWAs found</h3>
            <p className="text-gray-600 mb-4">
              Try adjusting your search criteria or explore different categories
            </p>
            <Button onClick={() => {
              setSearchTerm('')
              setCategoryFilter('all')
            }}>
              Clear Filters
            </Button>
          </div>
        )}
      </div>
    </div>
  )
} 